<style>

#myVideo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  width: 100%;
  height: 100%;
  z-index: 0;
  object-fit: cover;
  }

.front{
  background-size: cover !important;
  background-position: center !important;
}


.cardBox {
  float: left;
  font-size: 1.2em;
  margin: 1% 0 0 1%;
  perspective: 800px;
  transition: all 0.3s ease 0s;
  width: 23.7%;
}

.cardBox:hover .cardFlip {
  transform: rotateY( 180deg);
}

.cardFlip {
  background: #222;
  cursor: default;
  height: 300px;
  transform-style: preserve-3d;
  transition: transform 0.4s ease 0s;
  width: 100%;
  -webkit-animation: giro 1s 1;
  animation: giro 1s 1;
}

.cardFlip p {
  margin-bottom: 1.8em;
}

.cardFlip .back {
  backface-visibility: hidden;
  box-sizing: border-box;
  color: #555252;;
  display: block;
  font-size: 1em;
  height: 100%;
  padding: 0.8em;
  position: absolute;
  text-align: center;
  width: 100%;
}

.cardFlip .front strong {
  background: #fff;
  border-radius: 100%;
  color: #fff;
  font-size: 1.5em;
  line-height: 30px;
  padding: 0 7px 4px 6px;
}

.cardFlip .front{
  backface-visibility: hidden;
  box-sizing: border-box;
  color: #fff;
  display: block;
  font-size: 1em;
  height: 100%;
  padding: 0.8em;
  position: absolute;
  text-align: center;
  width: 100%;
}

.cardFlip .front h3{
  background-color: #33333359;
}

.cardFlip .back {
  transform: rotateY( 180deg);
  border: 3px solid #555252;
}

.cardFlip .back a {
  padding: 0.3em 0.5em;
  background: #333;
  color: #fff;
  text-decoration: none;
  border-radius: 1px;
  font-size: 0.9em;
  transition: all 0.2s ease 0s;
}

.cardFlip .back a:hover {
  background: #fff;
  color: #333;
  text-shadow: 0 0 1px #333;
}

.cardBox:nth-child(1) .cardFlip .back {
  background: white;
}

.cardBox:nth-child(2) .cardFlip .back {
  background: white;
}

.cardBox:nth-child(3) .cardFlip .back {
  background: white;
}

.cardBox:nth-child(4) .cardFlip .back {
  background: white;
}

.cardBox:nth-child(2) .cardFlip {
  -webkit-animation: giro 1.5s 1;
  animation: giro 1.5s 1;
}

.cardBox:nth-child(3) .cardFlip {
  -webkit-animation: giro 2s 1;
  animation: giro 2s 1;
}

.cardBox:nth-child(4) .cardFlip {
  -webkit-animation: giro 2.5s 1;
  animation: giro 2.5s 1;
}

@-webkit-keyframes giro {
  from {
    transform: rotateY( 180deg);
  }
  to {
    transform: rotateY( 0deg);
  }
}

@keyframes giro {
  from {
    transform: rotateY( 180deg);
  }
  to {
    transform: rotateY( 0deg);
  }
}

@media screen and (max-width: 767px) {
  .cardBox {
